body{
	height:100vh;
	width:100vw;
	margin:0;
	overflow:hidden;
}

#root {
	height:100%;
	width:100%;
}

#main-canvas {
	height:100%;
	width:100%;
	background-color:black;
	z-index:0;
}

#jet-plane {
	height:10vh;
	width:10vh;
	position:absolute;
	z-index:100;
	user-select:none;
}

.arrow {
	z-index:99;
	height:10vh;
	width:10vh;
	position:absolute;
	display:none;
	user-select:none;
}

#bugDiv {
	background-color:black;
	
	
}

.bug {
	height:10vh;
	width:10vh;
	z-index:104;
	background:red;
	position:absolute;
}

.bugRow {
	height:10vh;
}

